import React from 'react';
import './News.css';

const NewsTemp = () => {
    return(
        <React.Fragment>
            <div className="row -b-news">
                <div className="col-lg-2 news_col">
                    <img src="../images/download.jpg" alt="not found" className="img-responsive news_pic"/>
                </div>
                <div className="col-lg-10">
                    <p className="-b-news-heading">Lorem Ipsum is simply dummy text of the printing and typesetting </p>
                    <i>15 Aug 2015</i>
                    <p className="-b-news-content"><b>Place : </b>It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout.
                        The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using
                        'Content here, content here', </p>
                </div>
            </div>
            <hr />
        </React.Fragment>
    )
}

class News extends React.Component{
    render(){
        return(
            <div className="container-fluid">
                <h1>News</h1>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-8">
                            <NewsTemp />
                            <NewsTemp />
                            <NewsTemp />
                        </div>
                        <div className="col-lg-4">

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default News;